html {
  box-sizing: border-box;
  height: 100%;
}

*,
*::after,
*::before {
  box-sizing: inherit;
  margin: 0;
  padding: 0;
}

body {
  background-color: $white-color;
  color: $black-color;
  font-family: 'DM Sans', sans-serif;
  font-weight: 500;
  letter-spacing: 0.04em;
  line-height: 1.76;
  font-size: 14px;
  display: flex;
  flex-direction: column;
  height: 100%;
}
.section-title {
  font-family: 'Titan One', cursive;
}

.container {
  margin: 0 auto;
  width: 100%;
  @media screen and (min-width: 320px) {
    width: 100%;
    padding-left: 20px;
    padding-right: 20px;
  }

  @media screen and (min-width: 480px) {
    width: 480px;
    // да этой точки перелома в маккете нет, но при обсуждении с ментором
    // был затронут этот момент, слишком большие перепады по ширине экрана,
    // потому нужна еще и эта точка перелеома, и тут придеться
    // импровизировать так как дизайна нет, прделагаю делать что-то
    // середнее между 320px и 768px
  }

  @media screen and (min-width: 768px) {
    width: 768px;
    padding-left: 34px;
    padding-right: 34px;
  }

  @media screen and (min-width: 1280px) {
    width: 1280px;
    padding-right: 115px;
    padding-left: 115px;
  }
}

.visually-hidden {
  position: absolute;
  width: 1px;
  height: 1px;
  margin: -1px;
  border: 0;
  padding: 0;
  clip: rect(0 0 0 0);
  overflow: hidden;
}

ul {
  list-style: none;
}

address {
  font-style: normal;
}

img {
  display: block;
  max-width: 100%;
  height: auto;
}

.button {
  background-color: $white-color;
  color: $red-color;

  &:hover,
  &:focus,
  &-accent {
    background-color: $red-color;
    color: $white-color;
  }

  &-accent {

    &:hover,
    &:focus {
      background-color: $white-color;
      color: $red-color;
    }
  }
}

.button,
.button-accent {
  transition: background-color 1250ms $timing-func, color 1250ms $timing-func;
}